'use client';
import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { useInView } from 'react-intersection-observer';
import Image from 'next/image';

const companyServices = [
    {
        id: '1',
        heading: 'IT Infrastructure',
        discp: 'End-to-end solutions for network management, cloud integration, and infrastructure optimization.'
    },
    {
        id: '2',
        heading: 'Digital Transformation Consulting',
        discp: 'Helping organizations transition seamlessly into the digital age, boosting efficiency and innovation.'
    },
    {
        id: '3',
        heading: 'Quality Assurance & Monitoring',
        discp: 'Reliable surveillance and monitoring to ensure your systems perform optimally, reducing risks and downtime.'
    }
];
const headings = [
    ['Network Optimization'],
    ['Cloud Integration'],
    ['Data Center Solutions'],
    ['System Scalability']
];

const headingDiscp = [
    ['Streamlining network performance for reliable connectivity.'],
    ['Seamless cloud adoption for enhanced flexibility.'],
    ['Efficient data center management and support.'],
    ['Scalable infrastructure to support business growth.']
];

const companyInfraStructure = [
    {
        id: '1',
        image: '/gray.png',
        label: 'Network Optimization',
        discp: 'Streamlining network performance for reliable connectivity.'
    },
    {
        id: '2',
        image: '/yellow.png',
        label: 'Cloud Integration',
        discp: 'Seamless cloud adoption for enhanced flexibility.'
    },
    {
        id: '3',
        image: '/gray.png',
        label: 'Data Center Solutions',
        discp: 'Efficient data center management and support.'
    },
    {
        id: '4',
        image: '/yellow.png',
        label: 'System Scalability',
        discp: 'Scalable infrastructure to support business growth.'
    },
];

export default function CompanyStructure({onFinish}:any) {
    const [currentIndex, setCurrentIndex] = useState(0);
    const { ref, inView } = useInView({ threshold: 0.5 });

    useEffect(() => {
        const handleScroll = (event:any) => {
            event.preventDefault();

            setCurrentIndex((prevIndex) => {
                const nextIndex = event.deltaY > 0
                    ? (prevIndex + 1) % headings.length
                    : (prevIndex - 1 + headings.length) % headings.length;
                
                // If last heading is reached, trigger the transition
                if (nextIndex === 0 && event.deltaY > 0) {
                    onFinish();
                }
                
                return nextIndex;
            });
        };

        if (inView) {
            window.addEventListener('wheel', handleScroll, { passive: false });
        } else {
            window.removeEventListener('wheel', handleScroll);
        }

        return () => {
            window.removeEventListener('wheel', handleScroll);
        };
    }, [inView, onFinish]);


    return (
        <>
            <div ref={ref} className='grid  px-4 md:px-12 lg:px-24  content-center '>
                <div className='grid grid-cols-1 lg:grid-cols-2 justify-between lg:gap-28'>
                    <div className="flex flex-col py-12 lg:py-0">
                        <motion.h1
                            key={`heading-${currentIndex}`}
                            className="text-3xl md:text-4xl font-medium"
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            transition={{ duration: 0.5 }}
                        >
                            {headings[currentIndex]}
                        </motion.h1>
                        <motion.p
                            key={`headingDiscp-${currentIndex}`}
                            className="text-sm md:text-md font-medium text-[#00000099] pt-4 pb-16"
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            transition={{ duration: 0.5 }}
                        >
                            {headingDiscp[currentIndex]}
                        </motion.p>
                        <Image
                            src="/ITeqpt.jpeg"
                            width={487}
                            height={311}
                            className="lg:w-96 lg:h-56  md:w-96 md:h-56 rounded-[40px] object-cover"
                            alt="Hero section image"
                        />
                    </div>

                    <div className='grid grid-cols-1 items-center justify-center gap-8 md:gap-x-10 md:gap-y-7 lg:gap-x-20 lg:gap-y-5 md:grid-cols-1'>
                        {companyInfraStructure.map((structure) => {
                            return (
                                <>
                                    <motion.div
                                        key={structure.id}
                                        className="flex flex-row"
                                        initial={{ opacity: 0, y: 20 }}
                                        animate={inView ? { opacity: 1, y: 0 } : { opacity: 0, y: 20 }}
                                        transition={{ duration: 0.5 }}
                                    >

                                        <Image
                                            width={106}
                                            height={106}
                                            alt='IT Imge'
                                            src={structure.image}
                                            className='w-20 h-20 rounded-3xl'
                                        />
                                        <div className='flex flex-col ml-4 lg:ml-8 justify-center '>
                                            <p className="text-xs md:text-xl lg:text-lg font-semibold ">
                                                {structure.label}
                                            </p>
                                            <p className="text-xs md:text-sm lg:text-xs font-medium text-[#00000099] py-3">
                                                {structure.discp}
                                            </p>
                                        </div>
                                    </motion.div>
                                </>
                            );
                        })}
                    </div>
                </div>
            </div>
        </>
    )
}
